import { createCustomElement } from '@servicenow/ui-core';
import FileApi from 'fileapi';

import '@servicenow/now-icon';
import '@servicenow/now-text-link';

import { t } from 'sn-translate';

import {
	ACCEPTED_FILES,
	FILE_TYPES,
	FILE_SET,
	FILE_REMOVE,
	FILE_DROP,
	DRAGOVER_SET,
	UPLOAD_FILE,
	UPLOAD_STARTED,
	UPLOAD_SUCCESS,
	UPLOAD_FAILURE,
	PARSE_PREVIEW_IMG,
	SET_PREVIEW_IMG
} from './constants';
import uploadAttachment from '../helpers/upload-attachment';

import styles from './sn-aow-file-uploader-image.scss';

export const handleFileDropEffect = ({ action, dispatch }) => {
	const { file } = action.payload;

	// Set file in state
	dispatch(FILE_SET, file);

	// Set image url if applicable
	if (FILE_TYPES.image.includes(file.type)) {
		dispatch(PARSE_PREVIEW_IMG, file);
	}
};

export const parsePreviewImageEffect = ({ action, dispatch }) => {
	const file = action.payload;
	FileApi.readAsDataURL(file, evt => {
		if (evt.result) {
			return dispatch(SET_PREVIEW_IMG, { img: evt.result });
		}
	});
};

const saveFileReference = (tableName, tableSysId, fieldName, fileSysId) => {
	return fetch(
		`/api/now/table/${tableName}/${tableSysId}?sysparm_transaction_scope=${tableSysId}`,
		{
			method: 'PUT',
			body: JSON.stringify({
				sys_id: tableSysId,
				[fieldName]: fileSysId
			})
		}
	);
};

export const uploadImageEffect = async ({
	dispatch,
	state,
	action: { payload }
}) => {
	const {
		properties: { requestUrl },
		selectedFile
	} = state;
	const { table_sys_id, table_name, file_name } = payload;

	const reqBody = {
		table_sys_id,
		table_name,
		file_name,
		selectedFile
	};

	dispatch(UPLOAD_STARTED);

	if (!table_sys_id || !table_name) {
		return dispatch(UPLOAD_FAILURE, 'No table and/or sysId provided');
	}

	if (!selectedFile) {
		return dispatch(UPLOAD_FAILURE, 'No file chosen');
	}

	try {
		// Upload file to attachment table
		const uploadResult = await uploadAttachment(
			`${requestUrl}?sysparm_transaction_scope=${table_sys_id}`,
			selectedFile,
			reqBody
		);

		const {
			result: { sys_id }
		} = uploadResult;

		await saveFileReference(table_name, table_sys_id, file_name, sys_id);

		return dispatch(UPLOAD_SUCCESS, uploadResult);
	} catch (error) {
		return dispatch(UPLOAD_FAILURE, error);
	}
};

const actionHandlers = {
	[FILE_SET]: ({ action: { payload }, updateState }) =>
		updateState({
			selectedFile: payload,
			isComplete: false,
			isUploading: false
		}),
	[FILE_REMOVE]: ({ updateState }) =>
		updateState({
			selectedFile: null,
			selectedImage: null,
			isComplete: false,
			isUploading: false
		}),
	[SET_PREVIEW_IMG]: ({ action: { payload }, updateState }) =>
		updateState({
			selectedImage: payload.img
		}),
	[DRAGOVER_SET]: ({
		action: {
			payload: { isDragging }
		},
		updateState
	}) =>
		updateState({
			isDragging
		}),
	[UPLOAD_STARTED]: ({ updateState }) =>
		updateState({
			isUploading: true
		}),
	/**
	 * To upload a file, set a ref on <aow-file-uploader ref={createRef()} /> implementation
	 * then dispatch UPLOAD_FILE with request body: ref.current.dispatch(UPLOADFILE, options)
	 * Required fields for upload request body: {
	 * 	  table_name, (string, table name to attach file to)
	 *    table_sys_id, ( string, record id to attach file to)
	 *    file_name: (string, name of file)
	 * }
	 */
	[UPLOAD_FILE]: uploadImageEffect,
	[UPLOAD_SUCCESS]: ({
		action: {
			payload: { uploadResult }
		},
		updateState
	}) =>
		updateState({
			isUploading: false,
			isComplete: true,
			uploadResult
		}),
	[UPLOAD_FAILURE]: () => {},
	[FILE_DROP]: handleFileDropEffect,
	[PARSE_PREVIEW_IMG]: parsePreviewImageEffect
};

const FILE_TYPE_ERROR_MSG = t('Wrong file type');
const fileTypesString = ACCEPTED_FILES.image.join(', ');

function validateFileType(file) {
	const { type } = file;
	return FILE_TYPES.image.includes(type);
}

const AOWImageUploader = (state, { dispatch }) => {
	const {
		properties: { label, disabled },
		isDragging,
		selectedFile,
		selectedImage
	} = state;
	let inputRef;

	const classes = {
		'aow-image-uploader-drop-target': true,
		'-image-selected': selectedImage,
		'-dragging': isDragging
	};

	const onInputChange = event => {
		const file = event.target.files[0];
		const isValid = validateFileType(file);
		if (isValid) {
			dispatch(FILE_SET, file);
			dispatch(PARSE_PREVIEW_IMG, file);
		} else {
			dispatch(UPLOAD_FAILURE, FILE_TYPE_ERROR_MSG);
		}
	};

	const onFileDrop = event => {
		event.preventDefault();
		if (event.dataTransfer.items && !selectedFile) {
			if (event.dataTransfer.items[0].kind === 'file') {
				const file = event.dataTransfer.items[0].getAsFile();
				const isValid = validateFileType(file);
				if (isValid) {
					dispatch(PARSE_PREVIEW_IMG, file);
					dispatch(FILE_DROP, { file });
				} else {
					dispatch(UPLOAD_FAILURE, FILE_TYPE_ERROR_MSG);
				}
			}
		}
		dispatch(DRAGOVER_SET, { isDragging: false });
	};

	const onDragover = event => {
		event.preventDefault();
		!isDragging && dispatch(DRAGOVER_SET, { isDragging: true });
	};

	const onDragleave = event => {
		event.preventDefault();
		isDragging && dispatch(DRAGOVER_SET, { isDragging: false });
	};

	return (
		<section className="aow-image-uploader">
			<input
				ref={element => (inputRef = element)}
				accept={FILE_TYPES.image.join()}
				hidden
				on-change={onInputChange}
				type="file"
			/>
			<div
				class={classes}
				on-dragover={onDragover}
				on-dragleave={onDragleave}
				on-drop={onFileDrop}>
				{selectedFile && selectedImage ? (
					<img
						className="aow-image-uploader-preview-image"
						src={selectedImage}
						alt={selectedFile.name}
					/>
				) : (
					<div>
						<now-icon icon="upload-fill" size="lg" />
						<div>
							<now-text-link
								accessibleLabel={`${t(
									'Browse files. Accepted file types: {0}',
									fileTypesString
								)}`}
								on-click={() => !disabled && inputRef.click()}
								label={label}
								variant="primary"
								href="javascript:void(0)"
							/>
						</div>
					</div>
				)}
			</div>
			{selectedFile ? (
				<div className="aow-image-uploader-image-link">
					<now-icon icon="circle-close-outline" variant="primary" />
					<now-text-link
						label={t('Remove image')}
						on-click={() => {
							dispatch(FILE_REMOVE);
							inputRef.value = '';
						}}
						variant="primary"
						href="javascript:void(0)"
					/>
				</div>
			) : (
				<p className="aow-image-uploader-file-types">
					{fileTypesString.replace(/\./g, '').toUpperCase()}
				</p>
			)}
		</section>
	);
};

createCustomElement('sn-aow-file-uploader-image', {
	view: AOWImageUploader,
	initialState: {
		selectedFile: null,
		selectedImage: null,
		isDragging: false,
		isUploading: false,
		isComplete: false,
		uploadResult: null
	},
	properties: {
		/**
		 * Label to be displayed above within the drop target
		 * Assumed to be translated when passed in
		 * @type {string}
		 */
		label: { default: 'file', reflect: true },
		/**
		 * disabled - manually override upload button disabled prop
		 * @type {boolean}
		 */
		disabled: { default: false, reflect: true },
		/**
		 * requestUrl - url to POST upload
		 * @type {string}
		 */
		requestUrl: { default: '/api/now/attachment/upload' }
	},
	actionHandlers,
	styles
});
