import { createCustomElement } from '@servicenow/ui-core';

import '@servicenow/now-icon';
import '@servicenow/now-text-link';
import '@servicenow/now-progress-bar';

import { t } from 'sn-translate';

import {
	FILE_SET,
	FILE_REMOVE,
	DRAGOVER_SET,
	UPLOAD_STARTED,
	UPLOAD_SUCCESS,
	UPLOAD_FAILURE,
	FILETYPE_ERROR
} from './constants';
import styles from './sn-aow-file-uploader-spreadsheet.scss';

const FILE_TYPE_ERROR_MSG = t('Wrong file type');

export const actionHandlers = {
	[FILE_SET]: ({ updateState }) => {
		updateState({
			isComplete: false,
			isUploading: false
		});
	},
	[FILE_REMOVE]: ({ updateState }) =>
		updateState({
			isComplete: false,
			isUploading: false
		}),
	[DRAGOVER_SET]: ({
		action: {
			payload: { isDragging }
		},
		updateState
	}) =>
		updateState({
			isDragging
		}),
	[UPLOAD_STARTED]: ({ updateState }) =>
		updateState({
			isUploading: true
		}),
	[UPLOAD_SUCCESS]: ({ updateState }) =>
		updateState({
			isUploading: false,
			isComplete: true
		}),
	[UPLOAD_FAILURE]: ({ updateState }) =>
		updateState({
			isUploading: false
		}),
	[FILETYPE_ERROR]: ({ updateState }) =>
		updateState({
			isUploading: false
		})
};

const SNFileUploader = (state, { dispatch }) => {
	const {
		properties: {
			selectedFile,
			label,
			disabled,
			acceptedFiles,
			fileTypes,
			loadingValue,
			loading,
			loadingText
		},
		isDragging
	} = state;
	let inputRef;

	const acceptedFileTypes = [...fileTypes, ...acceptedFiles];

	function validateFileType(file) {
		const { type } = file;
		return acceptedFileTypes.includes(type);
	}

	const fileTypesString = acceptedFiles.join(', ');

	const classes = {
		'sn-aow-file-uploader-drop-target': true,
		'-dragging': isDragging,
		'-file-selected': selectedFile
	};

	const onInputChange = event => {
		const file = event.target.files[0];
		const isValid = validateFileType(file);

		if (isValid) {
			dispatch(FILE_SET, file);
		} else {
			dispatch(FILETYPE_ERROR, { error: FILE_TYPE_ERROR_MSG });
			dispatch(UPLOAD_FAILURE, FILE_TYPE_ERROR_MSG);
		}
	};

	const onFileDrop = event => {
		event.preventDefault();
		if (event.dataTransfer.items && !selectedFile) {
			if (event.dataTransfer.items[0].kind === 'file') {
				const file = event.dataTransfer.items[0].getAsFile();
				const isValid = validateFileType(file);
				if (isValid) {
					dispatch(FILE_SET, file);
				} else {
					dispatch(FILETYPE_ERROR, { error: FILE_TYPE_ERROR_MSG });
				}
			}
		}
		dispatch(DRAGOVER_SET, { isDragging: false });
	};

	const onDragover = event => {
		event.preventDefault();
		!isDragging && dispatch(DRAGOVER_SET, { isDragging: true });
	};

	const onDragleave = event => {
		event.preventDefault();
		isDragging && dispatch(DRAGOVER_SET, { isDragging: false });
	};

	return (
		<section className="sn-aow-file-uploader">
			<input
				ref={element => (inputRef = element)}
				accept={acceptedFiles.join()}
				hidden
				on-change={onInputChange}
				type="file"
			/>
			{loading ? (
				<div className="loader">
					<now-progress-bar
						value={loadingValue}
						size="sm"
						config-aria={{
							'aria-label': t('Uploading file...')
						}}
					/>
					<p className="loading-text">{loadingText}</p>
				</div>
			) : (
				<div
					class={classes}
					on-dragover={onDragover}
					on-dragleave={onDragleave}
					on-drop={onFileDrop}>
					{selectedFile ? (
						<div className="selected-file-container">
							<now-icon icon="check-fill" variant="secondary" size="md" />
							<p>{selectedFile.name}</p>
							<button
								className="remove-selected-file"
								on-click={() => {
									dispatch(FILE_REMOVE);
									inputRef.value = '';
								}}>
								<now-icon icon="trash-outline" variant="secondary" size="md" />
							</button>
						</div>
					) : (
						<div>
							<now-icon icon="upload-fill" size="lg" />
							<div>
								<now-text-link
									accessibleLabel={`${t(
										'Browse files. Accepted file types: {0}',
										fileTypesString
									)}`}
									on-click={() => !disabled && inputRef.click()}
									label={label}
									variant="primary"
									href="javascript:void(0)"
								/>
								<p className="sn-aow-file-uploader-file-types">
									{fileTypesString.replace(/\./g, '').toUpperCase()}{' '}
									{t('files accepted.')}
								</p>
							</div>
						</div>
					)}
				</div>
			)}
		</section>
	);
};

createCustomElement('sn-aow-file-uploader-spreadsheet', {
	view: SNFileUploader,
	initialState: {
		isDragging: false,
		isUploading: false,
		isComplete: false
	},
	properties: {
		/**
		 * File that is currently selected, passed in from the page
		 * @type {object}
		 */
		selectedFile: {
			default: null
		},
		/**
		 * Label to be displayed above within the drop target
		 * Assumed to be translated when passed in
		 * @type {string}
		 */
		label: { default: 'file', reflect: true },
		/**
		 * disabled - manually override upload button disabled prop
		 * @type {boolean}
		 */
		disabled: { default: false, reflect: true },
		/**
		 * requestUrl - url to POST upload
		 * @type {string}
		 */
		requestUrl: { default: '/api/now/attachment/upload' },
		acceptedFiles: { default: ['.xlsx'], reflect: true },
		fileTypes: {
			default: [
				'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
			],
			reflect: true
		},
		/**
		 * loading - shows the now progress bar or not
		 * @type {boolean}
		 */
		loading: { default: false },
		/**
		 * loadingValue - what to display the loading bar progress at between 0 and 1
		 * @type {number|null}
		 */
		loadingValue: {
			type: ['number', 'undefined'],
			default: undefined,
			minimum: 0,
			maximum: 1
		},
		/**
		 * loadingText - text below the now progress bar
		 * @type {string}
		 */
		loadingText: { default: 'Parsing file...' }
	},
	actionHandlers,
	styles
});
