import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-typeahead';
import { messagesSchema } from '@servicenow/now-input';
import {
	itemSchema,
	itemsSchema,
	constrainSchema
} from '@servicenow/now-dropdown';
import sliceTypeaheadItems from '../helpers/slice-typeahead-items';

import styles from './styles.scss';

const getSearchedItemsEffect = {
	modifier: { name: 'debounce', delay: 500 },
	effect: ({
		properties: { items, itemsLimit },
		action: {
			payload: { value }
		},
		updateState
	}) => {
		if (value.length > 2) {
			updateState({
				limitedItems: items
			});
		} else {
			updateState({
				limitedItems: sliceTypeaheadItems(items, itemsLimit)
			});
		}
	}
};

createCustomElement('sn-aow-typeahead', {
	view: ({ properties, limitedItems }) => {
		const { multi, iconName, iconSize } = properties;
		const typeaheadProperties = {
			...properties,
			items: limitedItems
		};
		const endSlot = iconName ? (
			<now-icon icon={iconName} size={iconSize} slot="end" />
		) : null;
		return (
			<div className="sn-aow-typeahead">
				{multi ? (
					<now-typeahead-multi part="now-typeahead" {...typeaheadProperties}>
						{endSlot}
					</now-typeahead-multi>
				) : (
					<now-typeahead part="now-typeahead" {...typeaheadProperties}>
						{endSlot}
					</now-typeahead>
				)}
			</div>
		);
	},
	initialState: {
		limitedItems: []
	},
	properties: {
		iconName: {
			default: ''
		},
		iconSize: {
			default: 'sm'
		},
		appendToPayload: {
			default: {}
		},
		/**
		 * A number that limits the number of results down in the dropdown
		 * @type {(number)}
		 * @uib.description Number that limits items to speed up typeahead
		 * @uib.defaultValue null
		 */
		itemsLimit: {
			default: 100
		},
		/**
		 * A boolean that determines if typeahead is multi or not
		 * @type {(boolean)}
		 * @uib.description boolean that determines to use <now-typeahead-multi /> or not
		 * @uib.defaultValue false
		 */
		multi: {
			default: false
		},
		/**
		 * An array of `DropdownListItem` or `DropdownListSection` objects that are used as
		 * possible items that the user can select from.
		 * @type {(Array.<DropdownListItem> | Array.<DropdownListSection>)}
		 * @uib.description Array of possible items that the user can select from
		 * @uib.defaultValue [{"id": "usr1","label": "User 1","sublabel": "user1@servicenow.com","avatarProps": {"userName": "User 1","imageSrc": "http://i.pravatar.cc/24?u=33","presence": "away"}},{"id": "usr2","label": "User 2","sublabel": "user2@servicenow.com","avatarProps": {"userName": "User 2","imageSrc": "http://i.pravatar.cc/24?u=34","presence": "busy"},"disabled": true},{"id": "usr3","label": "User 3","sublabel": "user3@servicenow.com","avatarProps": {"userName": "User 3","imageSrc": "http://i.pravatar.cc/24?u=35","presence": "offline"}}]
		 */
		items: {
			default: [],
			schema: itemsSchema
		},
		/**
		 * The id of the currently selected item. Use `manage-selected-item` to
		 * override the default behavior and handle the
		 * `NOW_TYPEAHEAD#SELECTED_ITEM_SET` action manually. Note that this will
		 * not prevent the value from showing in the input field. To do so, use
		 * `manage-value`.
		 * @type {(string|number|null)}
		 * @uib.description The ID of the selected item from the `items` array
		 * @uib.fieldType string
		 * @uib.typeMetadata.emptyStateBehavior NULL
		 */
		selectedItem: {
			schema: { type: ['string', 'number', 'null'] },
			dependencies: ['items']
		},
		/**
		 * An array of `DropdownListItem` representing each selected item. Use
		 * `manage-selected-items` to override the default behavior and handle the
		 * `NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET` action manually.
		 * @type {Array.<DropdownListItem>}
		 * @uib.description An array of items representing each selected item
		 * @uib.fieldType json
		 * @uib.defaultValue []
		 */
		selectedItems: {
			default: [],
			schema: { type: 'array', items: itemSchema },
			dependencies: ['items']
		},
		/**
		 * An object describing additional sizing constrains for the items list popover.
		 *
		 * Valid keys are (all are optional):
		 *
		 *  - width
		 *  - minWidth
		 *  - maxWidth
		 *  - height
		 *  - minHeight
		 *  - maxHeight
		 *
		 * The value for each property should be a positive integer representing
		 * a pixel value that will be applied to the corresponding content style.
		 * Alternatively, you can specify the string `"target"` for width,
		 * minWidth, or maxWidth to have that property match the width of the
		 * input element.
		 *
		 * E.g., To ensure the items list is at least 400x200 pixels:
		 *
		 *  {
		 *		minWidth: 400,
		 *		minHeight: 200
		 *  }
		 *
		 * The default values for this property are:
		 *
		 *  {
		 *		width: "target",
		 *		maxHeight: 300,
		 *  }
		 *
		 * @private
		 * @type {ItemsListConstrain}
		 * @typedef {Object} ItemsListConstrain
		 * @description Sizing constraints to enforce on the items list. For each
		 * property, a number can be specified which sets that constraint in
		 * pixels.  If the value is "target" for width, minWidth, or maxWidth,
		 * the items list is sized to match the width of the input field.
		 * @property {"target"|number} width
		 * @property {"target"|number} minWidth
		 * @property {"target"|number} maxWidth
		 * @property {number} height
		 * @property {number} minHeight
		 * @property {number} maxHeight
		 * @uib.label Item Size Constraints
		 * @uib.description Sizing constraints to enforce on the items list. For each property, a number can be specified which sets that constraint in pixels.  If the value is "target" for width, minWidth, or maxWidth, the items list is sized to match the width of the input field
		 */
		itemsListConstrain: { schema: constrainSchema },
		/**
		 * Indicates that focus should automatically be placed inside the input
		 * on page load.
		 * @type {boolean}
		 * @uib.label Auto-focus
		 * @uib.description Indicates that focus should automatically be placed inside the input on page load
		 */
		autofocus: { schema: { type: 'boolean' } },
		/**
		 * Indicates that the user should be able to interact with the field
		 * and change its value. Field will appear grayed out when disabled.
		 * @type {boolean}
		 * @uib.description Disable user click interactions
		 */
		disabled: { schema: { type: 'boolean' } },
		/**
		 * Controls how to filter the items as the user types into the input.
		 * - "managed" (default): no filtering will happen. Use this if you want to manually
		 *   filter or search for items as the user types.
		 * - "initial": items whose label or sublabel begins with the
		 *   input value will be displayed. This is case-insensitive.
		 * - "contains": items whose label or sublabel contains the input value
		 *   will be displayed. This is case-insensitive.
		 * @type {("managed"|"initial"|"contains")}
		 * @uib.label Search Behavior
		 * @uib.description Controls how to filter the items as the user types into the integrated search input
		 */
		search: {
			default: 'managed',
			schema: { type: 'string', enum: ['managed', 'initial', 'contains'] }
		},
		/**
		 * If set, an iconic button with popover will appear next to the field
		 * label for displaying additional hint text to the user, such as
		 * password requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 * @uib.label Helper text
		 * @uib.description If set, an iconic button with popover will appear next to the field label for displaying additional hint text to the user, such as password requirements or other instructions for completing the field. Use plain text to allow the text to be translatable
		 * @uib.fieldType string
		 * @uib.translatable true
		 */
		helperContent: {},
		/**
		 * Indicates that the field is in an incorrect state, which will be
		 * visually reflected in the field display. This property will be set
		 * automatically based on the current validity of the input field. Use
		 * `manage-invalid` to override the default behavior and handle the
		 * `NOW_TYPEAHEAD#INVALID_SET` action manually.
		 * @type {boolean}
		 * @uib.description Set this flag to indicate that the field is in an incorrect state, which will be visually reflected in the field display.
		 */
		invalid: { schema: { type: 'boolean' } },
		/**
		 * Label describing the overall purpose of the input field.
		 * @type {string}
		 * @uib.description Label describing the overall purpose of the select field
		 * @uib.defaultValue Label
		 * @uib.translatable true
		 */
		label: { schema: { type: 'string' } },
		/**
		 * Array of inline alert messages that are displayed below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.description Array of inline alert messages that are displayed below the field
		 * @uib.defaultValue []
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * Name of the field control. Dispatched along with the field value
		 * on user interaction.
		 * @type {string}
		 * @uib.description Name of the field control which will be included in dispatched actions
		 */
		name: { schema: { type: 'string' } },
		/**
		 * Indicates the field is optional and not required to be completed by
		 * the user before completing the form.
		 * @type {boolean}
		 * @uib.description Visually indicates that the field is optional
		 * @uib.defaultValue false
		 */
		optional: { schema: { type: 'boolean' } },
		/**
		 * Hint text displayed in the input when the user has not yet entered a
		 * value.
		 * @type {string}
		 * @uib.label Placeholder text
		 * @uib.description The placeholder property allows you to set a placeholder text for the input field. This text should hint to the user what input is expected.
		 * @uib.translatable true
		 */
		placeholder: { schema: { type: 'string' } },
		/**
		 * Indicates that the user cannot modify the value of the field, but
		 * it should still appear enabled.
		 * @type {boolean}
		 * @uib.label Read-only
		 * @uib.description By enabling this property, the user cannot modify the value of the input field. The input field is not disabled in this setting.
		 */
		readonly: { schema: { type: 'boolean' } },
		/**
		 * Indicates that the user must enter a value in the
		 * input field before they can proceed with completing the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user must enter a value in the field before proceeding.
		 * @uib.defaultValue false
		 */
		required: { schema: { type: 'boolean' } },
		/**
		 * When true, the required indicator is visually hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is visually hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: { default: false, schema: { type: 'boolean' } },
		/**
		 * The value of the input field. This will be updated automatically
		 * as the user types. Use `manage-value` to override this and handle
		 * the `NOW_TYPEAHEAD#VALUE_SET` action manually. Note that this will
		 * not prevent the selected items from showing in the dropdown. To do
		 * so, use `manage-selected-item`.
		 * @type {string}
		 * @uib.description Sets a value for the input field. Unless the input field has pre-populated data, this property should be left empty.
		 * @uib.translatable true
		 */
		value: { schema: { type: 'string' } },
		manageSelectedItems: { default: false }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({
			properties: { itemsLimit, items },
			updateState
		}) => {
			updateState({
				limitedItems: sliceTypeaheadItems(items, itemsLimit)
			});
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			properties: { items, itemsLimit },
			action: {
				payload: { name, value }
			},
			updateState
		}) => {
			switch (name) {
				case 'items':
					updateState({
						limitedItems: sliceTypeaheadItems(value, itemsLimit)
					});
					break;
				case 'itemsLimit':
					updateState({
						limitedItems: sliceTypeaheadItems(items, value)
					});
					break;
			}
		},
		'NOW_TYPEAHEAD#VALUE_SET': getSearchedItemsEffect,
		'NOW_TYPEAHEAD_MULTI#VALUE_SET': getSearchedItemsEffect
	},
	styles
});
