import FileApi from 'fileapi';
import authToken from './get-auth-token';

export default function uploadAttachment(url, file, reqBody) {
	return new Promise((resolve, reject) => {
		FileApi.upload({
			url,
			headers: { 'x-upload': 'fileapi', ...authToken },
			data: reqBody,
			files: [file],
			complete: (error, xhr) => {
				if (error || !xhr.response) {
					reject(error);
				}

				const response = JSON.parse(xhr.response);
				resolve(response);
			}
		});
	});
}

export function deleteAttachment({ attachmentSysId, appId }) {
	return fetch(
		`/api/now/attachment/${attachmentSysId}?sysparm_transaction_scope=${appId}`,
		{
			method: 'DELETE',
			headers: authToken
		}
	);
}
