import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import '@servicenow/now-button';
import '@servicenow/now-popover';

import styles from './styles.scss';

createCustomElement('sn-aow-form-popover', {
	view: state => (
		<div className="form-popover-content">{state.properties.content}</div>
	),
	properties: {
		content: {}
	},
	styles
});

createCustomElement('sn-aow-helper-content', {
	view: ({ properties: { helperContent, position, constrain } }) => (
		<now-popover
			className="now-form-field-popover"
			interaction-type="none"
			position={position}
			constrain={constrain}>
			<now-button-iconic
				slot="trigger"
				class="helper-content-button"
				icon="circle-info-outline"
				size="sm"
				variant="tertiary"
				config-aria={{
					'aria-label': t('More information')
				}}
				bare
				hide-padding
			/>
			<sn-aow-form-popover
				slot="content"
				content={helperContent}
				role="status"
			/>
		</now-popover>
	),
	properties: {
		helperContent: { default: '' },
		/**
		 * Optional sizing constraints to enforce on the popover content. Valid keys are:
		 *
		 *  - width
		 *  - minWidth
		 *  - maxWidth
		 *  - height
		 *  - minHeight
		 *  - maxHeight
		 *
		 * The value for each key must be a positive integer representing a
		 * pixel value to apply to the corresponding content style.
		 * Alternatively, you can specify the string `"target"` to have the content
		 * match the value of the target element.
		 *
		 * For example, to ensure the content is at least 400x200 pixels:
		 *
		 *  {
		 *		"minWidth": 400,
		 *		"minHeight": 200
		 *  }
		 *
		 * To ensure that the content is always as wide as its target:
		 *
		 *	{
		 *		"width": "target"
		 *	}
		 *
		 * @private
		 * @type {PopoverConstrain}
		 */
		constrain: {
			schema: {
				type: 'object',
				properties: {
					width: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					minWidth: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					maxWidth: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					height: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					minHeight: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					maxHeight: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					}
				}
			}
		}
	}
});
