import uploadAttachment from './upload-attachment';

const UPLOAD_STARTED = 'IMAGE_UPLOAD#STARTED';
const UPLOAD_SUCCESS = 'IMAGE_UPLOAD#SUCCESS';
const UPLOAD_FAILURE = 'IMAGE_UPLOAD#FAILURE';

export default async ({ dispatch, action: { payload } }) => {
	const { sys_id, selectedFile } = payload;
	const requestUrl = '/api/now/appcreator/app/upload';
	const reqBody = {
		sys_id,
		selectedFile
	};

	dispatch(UPLOAD_STARTED);

	if (!sys_id) {
		return dispatch(UPLOAD_FAILURE, 'No sysId provided');
	}

	if (!selectedFile) {
		return dispatch(UPLOAD_FAILURE, 'No file chosen');
	}

	try {
		// Upload file to attachment table
		const uploadResult = await uploadAttachment(
			`${requestUrl}?sysparm_transaction_scope=${sys_id}`,
			selectedFile,
			reqBody
		);

		return dispatch(UPLOAD_SUCCESS, uploadResult);
	} catch (error) {
		return dispatch(UPLOAD_FAILURE, error);
	}
};
