import { createCustomElement } from '@servicenow/ui-core';

import '../sn-aow-helper-content';

import { CHECKBOX_TOOLTIP__CHECKBOX_SLOT } from './constants';

import styles from './styles.scss';
import generateStyleToolTipContent from '../helpers/generate-style-tooltip-content';

createCustomElement('sn-aow-checkbox-tooltip', {
	view: ({ properties: { helperContent, constrain } }) => (
		<div className="sn-aow-checkbox-tooltip">
			<slot name={CHECKBOX_TOOLTIP__CHECKBOX_SLOT} />
			<sn-aow-helper-content
				helperContent={generateStyleToolTipContent(helperContent)}
				constrain={constrain}
			/>
		</div>
	),
	properties: {
		/**
		 * If set, an iconic button with a popover appears next to the field
		 * label to display additional hint text to the user, such as
		 * password requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 */
		helperContent: { default: '' },
		/**
		 * Optional sizing constraints to enforce on the popover content. Valid keys are:
		 *
		 *  - width
		 *  - minWidth
		 *  - maxWidth
		 *  - height
		 *  - minHeight
		 *  - maxHeight
		 *
		 * The value for each key must be a positive integer representing a
		 * pixel value to apply to the corresponding content style.
		 * Alternatively, you can specify the string `"target"` to have the content
		 * match the value of the target element.
		 *
		 * For example, to ensure the content is at least 400x200 pixels:
		 *
		 *  {
		 *		"minWidth": 400,
		 *		"minHeight": 200
		 *  }
		 *
		 * To ensure that the content is always as wide as its target:
		 *
		 *	{
		 *		"width": "target"
		 *	}
		 *
		 * @private
		 * @type {PopoverConstrain}
		 */
		constrain: {
			schema: {
				type: 'object',
				properties: {
					width: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					minWidth: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					maxWidth: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					height: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					minHeight: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					},
					maxHeight: {
						oneOf: [{ type: 'string', const: 'target' }, { type: 'number' }]
					}
				}
			}
		}
	},
	styles
});
