import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-aow-tile-flow.scss';

createCustomElement('sn-aow-tile-flow', {
	view(state) {
		const {
			items,
			items: { length: itemsLength }
		} = state.properties;

		return (
			<div
				className="sn-aow-tile-flow"
				style={{ '--sn-aow-tile-flow--count': itemsLength }}>
				{items.map((item, index) => {
					const { imgSrc, label } = item;

					return (
						<Fragment>
							<div className="tile">
								<img className="tile-img" alt="" src={imgSrc} />
								{typeof label === 'string' ? (
									<p className="tile-label">{label}</p>
								) : null}
							</div>
							{index === itemsLength - 1 ? null : <hr className="connector" />}
						</Fragment>
					);
				})}
			</div>
		);
	},

	properties: {
		items: { default: [] }
	},

	styles
});
