import { createCustomElement } from '@servicenow/ui-core';

import { TILE_BUTTON__CLICKED } from './constants';

import '../sn-aow-helper-content';

import styles from './styles.scss';
import generateStyleToolTipContent from '../helpers/generate-style-tooltip-content';

createCustomElement('sn-aow-tile-button', {
	view: (
		{
			properties: {
				id,
				disabled,
				isSelected,
				icon,
				text,
				helperContent,
				appendToPayload
			}
		},
		{ dispatch }
	) => {
		return (
			<button
				disabled={disabled}
				class={{
					'tile-container': true,
					'-is-selected': isSelected
				}}
				on-click={() => {
					const payload = { ...appendToPayload, id };
					dispatch(TILE_BUTTON__CLICKED, payload);
				}}>
				<img src={icon} alt={'icon'} />
				<p>
					{text}{' '}
					{helperContent ? (
						<sn-aow-helper-content
							helperContent={generateStyleToolTipContent(helperContent)}
						/>
					) : null}
				</p>
			</button>
		);
	},
	properties: {
		id: { default: '' },
		icon: { default: '' },
		text: { default: '' },
		disabled: { default: false },
		helperContent: { default: '' },
		isSelected: { default: false },
		appendToPayload: { default: null }
	},
	styles
});
