import { createCustomElement } from '@servicenow/ui-core';

import '@servicenow/now-tooltip';
import {
	AOW_INFO_SUMMARY_EXTENDED_CONTENT,
	AOW_INFO_SUMMARY_CUSTOM_CONTENT
} from './constants';

import styles from './styles.scss';

const TOOLTIP_DELAY = 300;

createCustomElement('sn-aow-info-summary', {
	view: ({ properties: { data }, ...state }, { updateState }) => {
		const hasIcon = !!data[0].icon;
		return (
			<div className="outer-container">
				<div
					className={`info-container ${
						hasIcon ? 'icon-layout' : `dynamic-${data.length}-column-layout`
					}`}>
					{data.map(
						({
							label = '',
							text = '',
							icon = null,
							maxCharLength = 22,
							custom = false
						} = {}) => {
							if (icon) {
								return <img src={icon} alt={name} title={text} />;
							}

							if (custom) {
								return (
									<slot name={`${AOW_INFO_SUMMARY_CUSTOM_CONTENT}_${label}`} />
								);
							}

							const currentElementName = `${label
								.toLowerCase()
								.replace(/\s/g, '')}Ref`;

							return (
								<dl
									className="info-text"
									hook-insert={({ elm }) =>
										setTimeout(() => updateState({ [currentElementName]: elm }))
									}
									hook-destroy={() =>
										setTimeout(() =>
											updateState({ [currentElementName]: null })
										)
									}>
									<dt>{label}</dt>
									<dd>{text}</dd>
									{text.length > maxCharLength ? (
										<now-tooltip
											content={text}
											delay={TOOLTIP_DELAY}
											id={'roleNameTooltip'}
											target-ref={state[currentElementName]}
										/>
									) : null}
								</dl>
							);
						}
					)}
				</div>
				<slot name={AOW_INFO_SUMMARY_EXTENDED_CONTENT} />
			</div>
		);
	},
	properties: {
		data: {
			// data should be an array of objects with the following options:

			/**
			 * {
			 *   label, (string)
			 *   text, (string)
			 *   icon, (string)
			 *   maxCharLength, (num)
			 *   custom (bool)
			 * }
			 */

			/**
			 * setting "custom: true" will not render the text column and will
			 * render a slot with the name "AOW_INFO_SUMMARY_EXTENDED_CONTENT_{label}"
			 */

			/**
			 * setting a value for "icon" (img src) will render the image, without text
			 */
			default: []
		}
	},
	styles
});
